.about-me-container {
  display: flex;
  align-items: center;
  max-width: 900px;
  margin: 50px auto 0; /* Added top margin for spacing */
  padding: 30px 40px; /* Padding inside the container */
  background-color: #f2f2f2; /* Light background for contrast */
  border-radius: 10px; /* Rounded corners */
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1); /* Subtle shadow */
  transition: transform 0.3s ease; /* Smooth transition */
}

.about-me-container:hover {
  transform: scale(1.02); /* Slightly enlarge on hover */
}

.about-me-text {
  flex: 1;
  padding: 20px;
  animation: fadeIn 1s; /* Fade-in animation */
}

.about-me-image {
  flex: 1;
  padding: 20px;
  display: flex;
  justify-content: center; /* Center image */
  align-items: center; /* Center image */
}

.profile-image {
  width: 100%;
  height: auto;
  max-width: 350px;
  border-radius: 10px;
  transition: transform 0.3s ease; /* Image transition */
}

.profile-image:hover {
  transform: scale(1.05); /* Enlarge image on hover */
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px); /* Start from below */
  }
  to {
    opacity: 1;
    transform: translateY(0); /* End at normal position */
  }
}

/* Mobile view */
@media (max-width: 600px) {
  .about-me-container {
    flex-direction: column; /* Stack elements vertically */
    align-items: flex-start; /* Align items to the start */
  }

  .about-me-text, .about-me-image {
    width: 100%; /* Make each section full width */
    padding: 10px; /* Adjust padding for mobile */
  }

  .profile-image {
    max-width: 150px; /* Reduce max width of the image for mobile */
  }
}

.fancy-button {
  background-color: #0056b3; /* Darker constant color for the button */
  color: white; /* White text color */
  padding: 15px 30px;
  border: none;
  border-radius: 50px;
  cursor: pointer;
  font-size: 18px;
  font-weight: bold;
  transition: all 0.3s ease;
  box-shadow: 0 8px 15px rgba(0, 0, 0, 0.3); /* Darker shadow for depth */
  margin-top: 20px;
}

.fancy-button:hover {
  background-image: linear-gradient(45deg, #00c6ff, #0072ff); /* Tech-themed gradient colors */
  box-shadow: 0 0 20px rgba(0, 198, 255, 0.5), 0 0 30px rgba(0, 114, 255, 0.5); /* Neon glow effect */
  transform: translateY(-3px); /* Raise effect */
  border: 1px solid #00c6ff; /* Light border on hover */
}
